.contact {
    margin: auto;
    margin-top: 10%;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact h1 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 3.4em;
    color: #E7DFDD;
}

.contact p {
    color: #E7DFDD;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.2em;
    margin-bottom: 5%;
}

.contact .contact-links {
    display: flex;
    margin-top: 2%;
    width: 50%;
    justify-content: space-around;

}
.contact .contact-links .fa-envelope:hover {
    color: #A239CA;
}

.contact .contact-links .fa-github:hover {
    color: #A239CA;
}

.contact .contact-links .fa-linkedin:hover {
    color: #A239CA;
}

@media (max-width: 1024px) {
  .contact {
    margin-top: 150px;
    width: 80%;
  }

  .contact .contact-links {
    width: 50%;
  }
 }

 @media (max-width: 600px) {
   .contact {
     margin-top: 25%;
   }

   .contact h1 {
       font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
       font-size: 3em;
       color: #E7DFDD;
   }

   .contact p {
       color: #E7DFDD;
       font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
       font-size: 1em;
       margin-bottom: 5%;
   }

   .contact .contact-links {
     width: 70%;
     margin-top: 50px;
   }
 }
